import React from "react";
import products from "./products.json";
import axios, { Axios } from "axios";
import { useState } from "react";

var today = new Date();
var dd = String(today.getDate()).padStart(2, "0");
var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
var yyyy = today.getFullYear();

today = dd + "-" + mm + "-" + yyyy;

// let a;
// a = Math.floor(Math.random() * products.length);
// let oldPriceLink = `https://api.coingecko.com/api/v3/coins/elrond-erd-2/history?date=${products[a].relDate}`;
// let currPriceLink = `https://api.coingecko.com/api/v3/coins/elrond-erd-2/history?date=${today}`;

function Hero() {
     const [Data, setData] = useState("");
     const [currData, setcurrData] = useState("");
     const [a, seta] = useState(Math.floor(Math.random() * products.length));

     let oldPriceLink = `https://api.coingecko.com/api/v3/coins/elrond-erd-2/history?date=${products[a].relDate}`;
     let currPriceLink = `https://api.coingecko.com/api/v3/coins/elrond-erd-2/history?date=${today}`;

     axios.get(oldPriceLink).then((response) => {
          setData(response.data.market_data.current_price.usd.toFixed(2));
     });

     axios.get(currPriceLink).then((response) => {
          setcurrData(response.data.market_data.current_price.usd.toFixed(2));
     });
     function incr() {
          if (a === products.length - 1) {
               seta(0);
          } else {
               seta(a + 1);
          }
     }
     function decr() {
          if (a === 0) {
               seta(products.length - 1);
          } else {
               seta(a - 1);
          }
     }

     return (
          <div className="flex flex-row items-center justify-center h-screen text-xl text-center space-y-1">
               <div className="flex flex-row h-screen items-center justify-center">
                    <div className="w-1/2 h-screen flex items-center justify-end">
                         <img src={`./images/${products[a].prodName}.png`} alt="" className="w-96 pt-12" />
                    </div>
                    <div className="w-full h-screen flex items-center justify-center flex-col p-3">
                         <p>If you have bought this {products[a].prodName}</p>
                         <p> on {products[a].relDate}</p>
                         <p>
                              The launch price was around <em className="not-italic font-semibold text-red-600">${products[a].relPrice}</em> or {(products[a].relPrice / Data).toFixed(2)} EGLD
                         </p>
                         <p></p>
                         {/* <p>
                    Today, {today}, the same {products[0].cat} <br />
                    worth around {(products[0].relPrice / currPriceLink).toFixed(2)} EGLD
               </p> */}
                         <p>
                              Today, {today}, those {(products[a].relPrice / Data).toFixed(2)} EGLD worth ${((products[a].relPrice / Data).toFixed(2) * currData).toFixed(2)}
                         </p>
                         <br />
                         <div className="space-x-10">
                              <button onClick={decr} className="bg-purple-700 p-1 px-2 rounded-xl">
                                   Prev
                              </button>
                              <button onClick={incr} className="bg-purple-700 p-1 px-2 rounded-xl">
                                   Next
                              </button>
                         </div>
                    </div>
               </div>
          </div>
     );
}

export default Hero;
